body {
  margin: 0;
  padding: 0;
}

.carrusel {
  width: 100%;
  /* background-color: pink; */
  height: 120px;
  margin-top: 15px;
  display: flex;
  align-items: center;
  border: 1px solid #d1811b;
}

.image-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
}

.image-container img {
  width: 85px;
  height: 85px;
  border-radius: 50%;
  border: 2px solid #6bd125;
  padding: 3px;
  margin: 0;
}

/* ------------------------------ */

.carrusel .glider-contain {
  /* background-color: blueviolet; */
  width: 90%;
}

.glider-track .glider-slide {
  min-width: 107px;
}

.home-container {
  height: 100vh;
  width: 100vw;
  padding-top: 70px;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.post {
  display: flex;
  flex-direction: column;
  max-width: 600px;
  width: 50%;
  height: 100%;
  /* background-color: aqua; */
  overflow-y: auto;
}

.post::-webkit-scrollbar {
  display: none;
}

